import React, { useState, useEffect, useRef } from 'react';
import {
  Layout,
  Card,
  Button,
  Input,
  Typography,
  Space,
  Row,
  Col,
  Tag,
  message,
  Divider
} from 'antd';
import {
  BugOutlined,
  DeleteOutlined,
  ReloadOutlined,
  HomeOutlined,
  WifiOutlined,
  DatabaseOutlined,
  PlayCircleOutlined,
  UserAddOutlined,
  LinkOutlined
} from '@ant-design/icons';
import socketManager from '../utils/socket';
import { storage } from '../utils/storage';

const { Header, Content } = Layout;
const { Title, Text } = Typography;

const DebugPage = () => {
  const [isConnected, setIsConnected] = useState(false);
  const [logs, setLogs] = useState([]);
  const [nickname, setNickname] = useState('');
  const [roomId, setRoomId] = useState('');
  const [userInfo, setUserInfo] = useState(null);
  const logsEndRef = useRef(null);

  const scrollToBottom = () => {
    logsEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [logs]);

  useEffect(() => {
    // 
    updateLocalStorageStatus();
    addLog('', 'info');

    // Socket
    socketManager.connect();

    // Socket
    socketManager.on('connect', () => {
      setIsConnected(true);
      addLog('Socket.io ', 'success');
    });

    socketManager.on('disconnect', () => {
      setIsConnected(false);
      addLog('Socket.io ', 'error');
    });

    socketManager.on('roomCreated', (data) => {
      addLog(` : ${JSON.stringify(data)}`, 'success');
      const newUserInfo = {
        userId: data.userId,
        roomId: data.roomId,
        userNumber: data.userNumber,
        isHost: data.isHost,
        nickname: nickname
      };
      storage.setUserInfo(newUserInfo);
      updateLocalStorageStatus();
    });

    socketManager.on('roomJoined', (data) => {
      addLog(` : ${JSON.stringify(data)}`, 'success');
      updateLocalStorageStatus();
    });

    socketManager.on('error', (error) => {
      addLog(` : ${error}`, 'error');
    });

    socketManager.on('userListUpdate', (users) => {
      addLog(` : ${users.length} `);
      users.forEach(user => {
        addLog(`  - ${user.nickname} (${user.number}) ${user.isHost ? '[]' : ''}`);
      });
    });

    socketManager.on('chatMessage', (data) => {
      if (data.type === 'system') {
        addLog(` : ${data.message}`);
      } else {
        addLog(` ${data.nickname}: ${data.message}`);
      }
    });

    return () => {
      socketManager.off('connect');
      socketManager.off('disconnect');
      socketManager.off('roomCreated');
      socketManager.off('roomJoined');
      socketManager.off('error');
      socketManager.off('userListUpdate');
      socketManager.off('chatMessage');
    };
  }, [nickname]);

  const addLog = (message, type = 'info') => {
    const timestamp = new Date().toLocaleTimeString();
    setLogs(prev => [...prev, { 
      id: Date.now() + Math.random(),
      timestamp, 
      message, 
      type 
    }]);
  };

  const clearLogs = () => {
    setLogs([]);
  };

  const updateLocalStorageStatus = () => {
    const userInfo = storage.getUserInfo();
    setUserInfo(userInfo);
  };

  const clearStorage = () => {
    storage.clearUserInfo();
    updateLocalStorageStatus();
    addLog(' localStorage', 'success');
  };

  const testCreateRoom = () => {
    if (!nickname.trim()) {
      addLog('', 'error');
      return;
    }
    addLog(`: ${nickname}`);
    socketManager.emit('createRoom', { nickname: nickname.trim() });
  };

  const testJoinRoom = () => {
    if (!roomId.trim() || !nickname.trim()) {
      addLog('', 'error');
      return;
    }
    const formattedRoomId = roomId.trim().toUpperCase();
    addLog(`: ${formattedRoomId}, : ${nickname}`);
    socketManager.emit('joinRoom', { 
      roomId: formattedRoomId, 
      nickname: nickname.trim() 
    });
  };

  const testReconnect = () => {
    if (!userInfo) {
      addLog('', 'error');
      return;
    }
    addLog(`: ${userInfo.roomId}, ID: ${userInfo.userId}`);
    socketManager.emit('reconnectToRoom', {
      roomId: userInfo.roomId,
      userId: userInfo.userId,
      nickname: userInfo.nickname || ''
    });
  };

  const goToMainPage = () => {
    window.location.href = '/';
  };

  const renderLog = (log) => {
    const getColor = (type) => {
      switch (type) {
        case 'error': return '#ff4d4f';
        case 'success': return '#52c41a';
        case 'warning': return '#faad14';
        default: return '#333';
      }
    };

    return (
      <div 
        key={log.id}
        style={{ 
          fontSize: '12px',
          fontFamily: 'monospace',
          color: getColor(log.type),
          marginBottom: '4px'
        }}
      >
        [{log.timestamp}] {log.message}
      </div>
    );
  };

  return (
    <Layout style={{ minHeight: '100vh', background: '#f0f2f5' }}>
      <Header style={{ 
        background: 'white',
        borderBottom: '1px solid #f0f0f0',
        padding: '0 24px'
      }}>
        <div style={{ 
          display: 'flex', 
          alignItems: 'center', 
          justifyContent: 'space-between',
          height: '100%'
        }}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <BugOutlined style={{ fontSize: '24px', color: '#1890ff', marginRight: '12px' }} />
            <Title level={3} style={{ margin: 0 }}>
              
            </Title>
          </div>
          <Button 
            icon={<HomeOutlined />}
            onClick={goToMainPage}
          >
            
          </Button>
        </div>
      </Header>

      <Content style={{ padding: '24px' }}>
        <Row gutter={[24, 24]}>
          {/*  */}
          <Col xs={24} lg={12}>
            <Card 
              title={
                <Space>
                  <WifiOutlined />
                  <span></span>
                </Space>
              }
              size="small"
            >
              <Tag 
                color={isConnected ? 'success' : 'error'}
                style={{ fontSize: '14px', padding: '4px 12px' }}
              >
                {isConnected ? ' ' : ' '}
              </Tag>
            </Card>
          </Col>

          {/* localStorage  */}
          <Col xs={24} lg={12}>
            <Card 
              title={
                <Space>
                  <DatabaseOutlined />
                  <span>localStorage </span>
                </Space>
              }
              size="small"
              extra={
                <Button 
                  size="small" 
                  danger 
                  onClick={clearStorage}
                  icon={<DeleteOutlined />}
                >
                  
                </Button>
              }
            >
              {userInfo ? (
                <div style={{ fontSize: '12px' }}>
                  <div><strong>ID:</strong> {userInfo.userId}</div>
                  <div><strong>:</strong> {userInfo.roomId}</div>
                  <div><strong>:</strong> {userInfo.nickname || ''}</div>
                  <div><strong>:</strong> {userInfo.isHost ? '' : ''}</div>
                </div>
              ) : (
                <Text type="secondary"></Text>
              )}
            </Card>
          </Col>

          {/*  */}
          <Col xs={24}>
            <Card 
              title={
                <Space>
                  <PlayCircleOutlined />
                  <span></span>
                </Space>
              }
            >
              <Row gutter={[16, 16]}>
                <Col xs={24} sm={8}>
                  <Input
                    placeholder=""
                    value={nickname}
                    onChange={(e) => setNickname(e.target.value)}
                    prefix={<UserAddOutlined />}
                  />
                </Col>
                <Col xs={24} sm={8}>
                  <Input
                    placeholder=""
                    value={roomId}
                    onChange={(e) => setRoomId(e.target.value.toUpperCase())}
                    maxLength={6}
                    prefix={<LinkOutlined />}
                  />
                </Col>
                <Col xs={24} sm={8}>
                  <Space wrap>
                    <Button type="primary" onClick={testCreateRoom}>
                      
                    </Button>
                    <Button onClick={testJoinRoom}>
                      
                    </Button>
                    <Button onClick={testReconnect}>
                      
                    </Button>
                  </Space>
                </Col>
              </Row>
            </Card>
          </Col>

          {/*  */}
          <Col xs={24}>
            <Card 
              title={
                <Space>
                  <BugOutlined />
                  <span></span>
                </Space>
              }
              extra={
                <Button 
                  size="small" 
                  danger 
                  onClick={clearLogs}
                  icon={<DeleteOutlined />}
                >
                  
                </Button>
              }
            >
              <div
                style={{
                  background: '#f5f5f5',
                  padding: '12px',
                  borderRadius: '6px',
                  maxHeight: '400px',
                  overflowY: 'auto',
                  border: '1px solid #d9d9d9'
                }}
              >
                {logs.map(renderLog)}
                <div ref={logsEndRef} />
              </div>
            </Card>
          </Col>
        </Row>
      </Content>
    </Layout>
  );
};

export default DebugPage; 